/* Base Styles */
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  transition: background-color 0.5s ease, color 0.5s ease;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, sans-serif;
  background: var(--bg-primary);
  color: var(--text-primary);
}

html {
  scroll-behavior: smooth;
}

/* Theme Variables */
:root {
  /* Dark Mode (Default) */
  --bg-primary: #0f0f23;
  --bg-secondary: #1a1a2e;
  --bg-tertiary: #16213e;
  --text-primary: #e2e8f0;
  --text-secondary: rgba(255, 255, 255, 0.8);
  --text-tertiary: rgba(255, 255, 255, 0.6);
  --accent-primary: #667eea;
  --accent-secondary: #764ba2;
  --accent-tertiary: #f093fb;
  --card-bg: rgba(255, 255, 255, 0.05);
  --card-border: rgba(255, 255, 255, 0.1);
  --nav-bg: rgba(15, 15, 35, 0.1);
  --nav-scrolled-bg: rgba(15, 15, 35, 0.95);
  --nav-link-color: #fff;
  --nav-active-color: #667eea;
  --nav-active-bg: rgba(102, 126, 234, 0.2);
  --nav-indicator-bg: linear-gradient(90deg, #667eea, #764ba2);
  --button-primary-bg: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  --button-secondary-color: #f093fb;
  --button-secondary-border: rgba(240, 147, 251, 0.3);
  --tag-bg: rgba(102, 126, 234, 0.1);
  --tag-color: #667eea;
  --tag-border: rgba(102, 126, 234, 0.3);
  --section-bg-1: linear-gradient(180deg, #0f0f23 0%, #1a1a2e 100%);
  --section-bg-2: linear-gradient(180deg, #1a1a2e 0%, #16213e 100%);
  --section-bg-3: linear-gradient(180deg, #16213e 0%, #0f0f23 100%);
  --orb-bg-1: radial-gradient(circle, rgba(102, 126, 234, 0.15) 0%, transparent 70%);
  --orb-bg-2: radial-gradient(circle, rgba(118, 75, 162, 0.15) 0%, transparent 70%);
  --orb-bg-3: radial-gradient(circle, rgba(240, 147, 251, 0.15) 0%, transparent 70%);
  --popup-bg: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
}

[data-theme="light"] {
  --bg-primary: #f0f2f5;
  --bg-secondary: #ffffff;
  --bg-tertiary: #ffffff;
  --text-primary: #1e293b;
  --text-secondary: #475569;
  --text-tertiary: #64748b;
  --accent-primary: #1e40af;
  --accent-secondary: #3b82f6;
  --accent-tertiary: #9333ea;
  --card-bg: rgba(255, 255, 255, 0.8);
  --card-border: rgba(0, 0, 0, 0.1);
  --nav-bg: rgba(255, 255, 255, 0.1);
  --nav-scrolled-bg: rgba(255, 255, 255, 0.95);
  --nav-link-color: #1e293b;
  --nav-active-color: #1e40af;
  --nav-active-bg: rgba(30, 64, 175, 0.2);
  --nav-indicator-bg: linear-gradient(90deg, #1e40af, #3b82f6);
  --button-primary-bg: linear-gradient(135deg, #1e40af 0%, #3b82f6 100%);
  --button-secondary-color: #9333ea;
  --button-secondary-border: rgba(147, 51, 234, 0.3);
  --tag-bg: rgba(30, 64, 175, 0.1);
  --tag-color: #1e40af;
  --tag-border: rgba(30, 64, 175, 0.3);
  --section-bg-1: linear-gradient(180deg, #f0f2f5 0%, #ffffff 100%);
  --section-bg-2: linear-gradient(180deg, #ffffff 0%, #e2e8f0 100%);
  --section-bg-3: linear-gradient(180deg, #e2e8f0 0%, #f0f2f5 100%);
  --orb-bg-1: radial-gradient(circle, rgba(30, 64, 175, 0.15) 0%, transparent 70%);
  --orb-bg-2: radial-gradient(circle, rgba(59, 130, 246, 0.15) 0%, transparent 70%);
  --orb-bg-3: radial-gradient(circle, rgba(147, 51, 234, 0.15) 0%, transparent 70%);
  --popup-bg: linear-gradient(135deg, #1e40af 0%, #3b82f6 100%);
}

/* Container */
.container {
  line-height: 1.6;
  color: var(--text-primary);
  position: relative;
  background: var(--bg-primary);
  transition: background 0.5s ease, color 0.5s ease;
}

/* Cursor Trail */
.cursor-trail {
  position: fixed;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  pointer-events: none;
  z-index: 9998;
  mix-blend-mode: screen;
  background: var(--orb-bg-1);
}

/* Particles */
.particles-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 1;
  overflow: hidden;
}

.floating-particle {
  position: absolute;
  width: 2px;
  height: 2px;
  border-radius: 50%;
  background: var(--text-tertiary);
  filter: blur(1px);
}

/* Loader */
.loader {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: var(--bg-primary);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
  overflow: hidden;
  transition: background 0.5s ease;
}

.loader-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 2rem;
  z-index: 2;
  padding: 2rem;
}

.loader-logo {
  position: relative;
  width: 120px;
  height: 120px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  background: var(--card-bg);
  border: 1px solid var(--card-border);
  transition: background 0.5s ease, border 0.5s ease;
}

.loader-logo-text {
  font-size: 3rem;
  font-weight: 800;
  background: var(--button-primary-bg);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  position: relative;
  z-index: 2;
}

.loader-logo-glow {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background: var(--orb-bg-1);
}

.loading-bar {
  width: 300px;
  height: 4px;
  background: var(--card-border);
  border-radius: 2px;
  overflow: hidden;
}

.loading-progress {
  width: 100%;
  height: 100%;
  background: var(--button-primary-bg);
}

.loading-text {
  color: var(--text-secondary);
  font-size: 1rem;
  font-weight: 300;
}

.loader-particles {
  position: absolute;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.particle {
  position: absolute;
  width: 2px;
  height: 2px;
  border-radius: 50%;
  background: var(--text-tertiary);
  filter: blur(1px);
}

/* Navigation */
.nav {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  border-bottom: 1px solid var(--card-border);
  z-index: 1000;
  padding: 1rem 0;
  background: var(--nav-bg);
  transition: background 0.5s ease;
}

.nav-container {
  max-width: 1400px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 2rem;
}

.logo {
  position: relative;
  font-size: 1.8rem;
  font-weight: 800;
  cursor: pointer;
  color: var(--text-primary);
}

.logo img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  object-fit: contain;
}

.logo-text {
  background: var(--button-primary-bg);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  position: relative;
  z-index: 2;
}

.logo-glow {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: var(--orb-bg-1);
}

.nav-links {
  display: flex;
  gap: 1rem;
  align-items: center;
}

.nav-link {
  position: relative;
  text-decoration: none;
  font-weight: 500;
  padding: 0.5rem 1.5rem;
  border-radius: 30px;
  color: var(--nav-link-color);
}

.nav-link.active {
  color: var(--nav-active-color);
  background: var(--nav-active-bg);
}

.nav-indicator {
  position: absolute;
  bottom: -5px;
  left: 50%;
  transform: translateX(-50%);
  width: 20px;
  height: 2px;
  background: var(--nav-indicator-bg);
  border-radius: 1px;
}

/* Theme Toggle */
.theme-toggle {
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--nav-link-color);
  border-radius: 50%;
}

.theme-toggle:hover {
  color: var(--accent-primary);
  transform: scale(1.1);
}

/* Hero Section */
.hero {
  min-height: 100vh;
  display: flex;
  align-items: center;
  position: relative;
  padding: 0 2rem;
  overflow: hidden;
  background: var(--section-bg-1);
  transition: background 0.5s ease;
}

.hero-background {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
}

.gradient-orb-1 {
  position: absolute;
  top: 20%;
  left: 10%;
  width: 300px;
  height: 300px;
  background: var(--orb-bg-1);
  border-radius: 50%;
}

.gradient-orb-2 {
  position: absolute;
  top: 60%;
  right: 15%;
  width: 200px;
  height: 200px;
  background: var(--orb-bg-2);
  border-radius: 50%;
}

.gradient-orb-3 {
  position: absolute;
  bottom: 10%;
  left: 50%;
  width: 150px;
  height: 150px;
  background: var(--orb-bg-3);
  border-radius: 50%;
}

.hero-content {
  max-width: 1400px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
  align-items: center;
  width: 100%;
  z-index: 2;
  position: relative;
}

.hero-text {
  color: var(--text-primary);
}

.hero-greeting {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 1rem;
  color: var(--accent-primary);
  font-size: 1rem;
  font-weight: 500;
}

.greeting-icon {
  width: 18px;
  height: 18px;
}

.hero-title {
  font-size: 3.5rem;
  font-weight: 800;
  line-height: 1.2;
  margin-bottom: 1.5rem;
  position: relative;
  color: var(--text-primary);
}

.hero-name {
  background: var(--button-primary-bg);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.hero-roles {
  display: flex;
  gap: 1rem;
  margin-bottom: 2rem;
  flex-wrap: wrap;
}

.role-tag {
  background: var(--tag-bg);
  color: var(--tag-color);
  padding: 0.5rem 1rem;
  border-radius: 5px;
  font-size: 0.9rem;
  font-weight: 500;
  border: 1px solid var(--tag-border);
  cursor: default;
}

.hero-subtitle {
  font-size: 1.1rem;
  line-height: 1.8;
  margin-bottom: 2.5rem;
  color: var(--text-secondary);
  max-width: 600px;
}

.hero-buttons {
  display: flex;
  gap: 1.5rem;
  align-items: center;
}

.primary-button {
  background: var(--button-primary-bg);
  color: white;
  border: none;
  padding: 0.8rem 1.8rem;
  border-radius: 30px;
  font-size: 1rem;
  font-weight: 500;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  z-index: 1;
}

.button-glow {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 150%;
  height: 150%;
  background: radial-gradient(circle, rgba(255, 255, 255, 0.3) 0%, transparent 70%);
  z-index: -1;
}

.secondary-button {
  background: transparent;
  color: var(--button-secondary-color);
  border: 1px solid var(--button-secondary-border);
  padding: 0.8rem 1.8rem;
  border-radius: 30px;
  font-size: 1rem;
  font-weight: 500;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  cursor: pointer;
}

.button-icon {
  width: 18px;
  height: 18px;
}

.hero-visual {
  position: relative;
  height: 500px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.hero-animation {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.code-matrix {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 0.5rem;
  padding: 2rem;
  font-family: 'Courier New', monospace;
  font-size: 0.9rem;
  color: var(--text-tertiary);
  z-index: 1;
}

.code-row {
  display: flex;
  justify-content: space-between;
}

.code-char {
  display: inline-block;
}

.center-orb {
  position: relative;
  width: 300px;
  height: 300px;
  border-radius: 50%;
  background: var(--card-bg);
  border: 1px solid var(--card-border);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2;
}

.orb-core {
  width: 60%;
  height: 60%;
  object-fit: contain;
  border-radius: 50%;
  z-index: 3;
}

.orb-ring-1 {
  position: absolute;
  width: 120%;
  height: 120%;
  border-radius: 50%;
  border: 1px solid var(--tag-border);
  z-index: 1;
}

.orb-ring-2 {
  position: absolute;
  width: 140%;
  height: 140%;
  border-radius: 50%;
  border: 1px solid var(--tag-border);
  z-index: 1;
}

.orb-ring-3 {
  position: absolute;
  width: 160%;
  height: 160%;
  border-radius: 50%;
  border: 1px solid var(--tag-border);
  z-index: 1;
}

.scroll-indicator {
  position: absolute;
  bottom: 2rem;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
  color: var(--text-tertiary);
  z-index: 2;
}

.scroll-text {
  font-size: 0.8rem;
  font-weight: 300;
  letter-spacing: 0.1em;
}

.scroll-icon {
  width: 20px;
  height: 20px;
}

.scroll-line {
  width: 1px;
  height: 50px;
  background: linear-gradient(to bottom, var(--accent-primary), transparent);
}

/* About Section */
.about {
  position: relative;
  padding: 6rem 2rem;
  background: var(--section-bg-1);
}

.section-container {
  max-width: 1400px;
  margin: 0 auto;
}

.section-header {
  margin-bottom: 4rem;
  text-align: center;
}

.section-title {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 1rem;
  display: inline-flex;
  align-items: center;
  position: relative;
}

.title-number {
  color: var(--accent-primary);
  margin-right: 0.5rem;
  font-size: 1.5rem;
  font-weight: 400;
}

.section-subtitle {
  font-size: 1.1rem;
  color: var(--text-tertiary);
  font-weight: 300;
}

.about-content {
  display: grid;
  grid-template-columns: 1fr;
  gap: 2rem;
  align-items: flex-start;
}

.about-layout-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 2rem;
}

.about-left-column,
.about-middle-column,
.about-right-column {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.about-card {
  background: var(--card-bg);
  border: 1px solid var(--card-border);
  border-radius: 20px;
  padding: 1.5rem;
}

.about-card-title {
  font-size: 1.5rem;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 1.5rem;
}

.about-paragraph {
  font-size: 1rem;
  line-height: 1.8;
  color: var(--text-secondary);
  margin-bottom: 1rem;
}

.stats-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
}

.stat-card {
  background: var(--card-bg);
  border: 1px solid var(--card-border);
  border-radius: 15px;
  padding: 1.5rem;
  text-align: center;
}

.stat-number {
  font-size: 2rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 0.5rem;
}

.stat-label {
  font-size: 0.9rem;
  color: var(--text-tertiary);
  font-weight: 300;
}

.skills-container {
  background: var(--card-bg);
  border: 1px solid var(--card-border);
  border-radius: 20px;
  padding: 2rem;
}

.skills-title {
  font-size: 1.5rem;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 2rem;
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1.5rem;
}

.skill-card {
  background: var(--card-bg);
  border: 1px solid var(--card-border);
  border-radius: 15px;
  padding: 1.5rem;
}

.skill-icon {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--card-bg);
  border-radius: 10px;
  margin-bottom: 1rem;
}

.skill-name {
  font-size: 1rem;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 1rem;
}

.skill-bar-container {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.skill-bar {
  flex: 1;
  height: 6px;
  background: var(--card-border);
  border-radius: 3px;
  overflow: hidden;
}

.skill-progress {
  height: 100%;
  border-radius: 3px;
  background: var(--button-primary-bg);
}

.skill-level {
  font-size: 0.8rem;
  color: var(--text-tertiary);
  font-weight: 300;
}

/* Projects Section */
.projects {
  position: relative;
  padding: 8rem 2rem;
  background: var(--section-bg-2);
  transition: background 0.5s ease;
}

.projects-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
  gap: 2rem;
  margin-top: 3rem;
}

.project-card {
  position: relative;
  background: var(--card-bg);
  border: 1px solid var(--card-border);
  border-radius: 20px;
  overflow: hidden;
  z-index: 1;
}

.featured-project {
  box-shadow: 0 20px 50px -10px rgba(102, 126, 234, 0.2);
}

.project-number {
  position: absolute;
  top: 1rem;
  right: 1rem;
  font-size: 3rem;
  font-weight: 800;
  color: var(--text-tertiary);
  z-index: 1;
}

.project-image {
  position: relative;
  height: 250px;
  overflow: hidden;
}

.project-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.project-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: var(--bg-primary);
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
}

.project-links {
  display: flex;
  gap: 2rem;
}

.project-link {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
  color: var(--text-primary);
  text-decoration: none;
  font-size: 0.9rem;
  font-weight: 500;
}

.featured-badge {
  position: absolute;
  top: 1rem;
  left: 1rem;
  background: var(--tag-bg);
  border: 1px solid var(--tag-border);
  color: var(--tag-color);
  padding: 0.3rem 0.8rem;
  border-radius: 30px;
  font-size: 0.8rem;
  font-weight: 500;
  display: flex;
  align-items: center;
  gap: 0.3rem;
  z-index: 2;
}

.project-content {
  padding: 1.5rem;
}

.project-title {
  font-size: 1.3rem;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 1rem;
}

.project-description {
  font-size: 0.95rem;
  line-height: 1.6;
  color: var(--text-secondary);
  margin-bottom: 1.5rem;
}

.project-tech {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.tech-tag {
  background: var(--tag-bg);
  color: var(--tag-color);
  padding: 0.3rem 0.8rem;
  border-radius: 30px;
  font-size: 0.8rem;
  font-weight: 500;
  border: 1px solid var(--tag-border);
}

.project-glow {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 150%;
  height: 150%;
  background: var(--orb-bg-1);
  z-index: -1;
}

/* Contact Section */
.contact {
  position: relative;
  padding: 8rem 2rem;
  background: var(--section-bg-3);
  overflow: hidden;
  transition: background 0.5s ease;
}

.contact-background {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
}

.contact-orb-1 {
  position: absolute;
  top: 20%;
  left: 10%;
  width: 300px;
  height: 300px;
  background: var(--orb-bg-1);
  border-radius: 50%;
}

.contact-orb-2 {
  position: absolute;
  bottom: 20%;
  right: 10%;
  width: 200px;
  height: 200px;
  background: var(--orb-bg-3);
  border-radius: 50%;
}

.contact-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
  position: relative;
  z-index: 2;
}

.contact-text {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.contact-card {
  background: var(--card-bg);
  backdrop-filter: blur(10px);
  border: 1px solid var(--card-border);
  border-radius: 20px;
  padding: 2rem;
  transition: all 0.5s ease;
}

.contact-card-title {
  font-size: 1.5rem;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 1.5rem;
  background: var(--button-primary-bg);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.contact-description {
  font-size: 1rem;
  line-height: 1.8;
  color: var(--text-secondary);
  margin-bottom: 2rem;
}

.contact-stats {
  display: flex;
  gap: 2rem;
  margin-bottom: 2rem;
}

.contact-stat {
  display: flex;
  align-items: center;
  gap: 1rem;
  background: var(--card-bg);
  border: 1px solid var(--card-border);
  border-radius: 15px;
  padding: 1rem;
  transition: all 0.5s ease;
}

.contact-stat-icon {
  font-size: 1.5rem;
  color: var(--accent-primary);
}

.contact-stat-number {
  font-size: 1.2rem;
  font-weight: 600;
  color: var(--text-primary);
}

.contact-stat-label {
  font-size: 0.8rem;
  color: var(--text-tertiary);
  font-weight: 300;
}

.social-links {
  display: flex;
  gap: 1rem;
}

.social-link {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  background: var(--tag-bg);
  color: var(--tag-color);
  padding: 0.8rem 1.2rem;
  border-radius: 30px;
  font-size: 0.9rem;
  font-weight: 500;
  text-decoration: none;
  border: 1px solid var(--tag-border);
  position: relative;
  overflow: hidden;
  z-index: 1;
}

.social-glow {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 150%;
  height: 150%;
  background: radial-gradient(circle, rgba(255, 255, 255, 0.3) 0%, transparent 70%);
  z-index: -1;
}

.contact-form-container {
  background: var(--card-bg);
  backdrop-filter: blur(10px);
  border: 1px solid var(--card-border);
  border-radius: 20px;
  padding: 2rem;
}

.contact-form {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.form-group {
  position: relative;
}

.form-input {
  width: calc(100% - 2rem - 2px);
  background: var(--card-bg);
  border: 1px solid var(--card-border);
  border-radius: 10px;
  padding: 1rem;
  font-size: 1rem;
  color: var(--text-primary);
  outline: none;
}

.input-glow {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  background: var(--orb-bg-1);
  z-index: -1;
  border-radius: 10px;
}

.form-textarea {
  width: calc(100% - 2rem - 2px);
  background: var(--card-bg);
  border: 1px solid var(--card-border);
  border-radius: 10px;
  padding: 1rem;
  font-size: 1rem;
  color: var(--text-primary);
  outline: none;
  resize: none;
}

.submit-button {
  background: var(--button-primary-bg);
  color: white;
  border: none;
  padding: 1rem;
  border-radius: 10px;
  font-size: 1rem;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  z-index: 1;
  margin-top: 1rem;
}

.submit-glow {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 150%;
  height: 150%;
  background: radial-gradient(circle, rgba(255, 255, 255, 0.3) 0%, transparent 70%);
  z-index: -1;
}

/* Footer */
.footer {
  position: relative;
  padding: 3rem 2rem;
  background: var(--bg-primary);
  border-top: 1px solid var(--card-border);
  text-align: center;
  transition: background 0.5s ease;
}

.footer-content {
  max-width: 1400px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.5rem;
}

.footer-logo {
  position: relative;
  font-size: 1.8rem;
  font-weight: 800;
  cursor: pointer;
  color: var(--text-primary);
}

.footer-logo-text {
  background: var(--button-primary-bg);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  position: relative;
  z-index: 2;
}

.footer-logo-glow {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: var(--orb-bg-1);
}

.footer-text {
  font-size: 0.9rem;
  color: var(--text-tertiary);
  font-weight: 300;
}

.form-success {
  background: var(--card-bg);
  color: var(--text-primary);
  padding: 1rem;
  border-radius: 0.5rem;
  margin-bottom: 1rem;
  text-align: center;
  border: 1px solid var(--card-border);
}

/* Popup Notification */
.connected-popup {
  position: fixed;
  transform: translateX(-50%);
  z-index: 1000;
  display: flex;
  flex-direction: column;
  align-items: center;
  pointer-events: none;
}

.popup-content {
  background: var(--popup-bg);
  color: white;
  padding: 15px 20px;
  border-radius: 12px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
  display: flex;
  align-items: center;
  gap: 12px;
  max-width: 300px;
  margin-bottom: 10px;
}

.popup-arrow {
  color: var(--accent-primary);
  margin-bottom: 5px;
}

.popup-icon {
  display: flex;
  padding: 8px;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 50%;
}

.popup-message {
  font-size: 0.9rem;
  font-weight: 500;
}

.popup-progress {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: rgba(255, 255, 255, 0.5);
  border-radius: 0 0 12px 12px;
  transform-origin: left;
}

.popup-connector {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 999;
}

/* Mobile Navigation */
.nav-menu {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: var(--nav-scrolled-bg);
  backdrop-filter: blur(10px);
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 999;
  padding: 2rem;
}

.nav-menu.active {
  display: flex;
}

.nav-menu .nav-link {
  font-size: 1.5rem;
  margin: 1rem 0;
  padding: 1rem;
}

.nav-toggle {
  display: none;
  font-size: 1.5rem;
  background: none;
  border: none;
  color: var(--nav-link-color);
  cursor: pointer;
  padding: 0.5rem;
}

/* Media Queries for Mobile Responsiveness */
@media (min-width: 1024px) {
  .hero-content {
    grid-template-columns: 1fr 1fr;
  }

  .about-layout-grid {
    grid-template-columns: repeat(3, 1fr);
  }

  .stats-grid {
    grid-template-columns: repeat(3, 1fr);
  }

  .skills-grid {
    grid-template-columns: repeat(2, 1fr);
  }

  .projects-container {
    grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
  }

  .contact-content {
    grid-template-columns: 1fr 1fr;
  }
}

@media (max-width: 1024px) {
  .hero-content {
    grid-template-columns: 1fr;
    gap: 2rem;
    padding: 0 1rem;
  }

  .hero-visual {
    height: 300px;
    order: -1;
  }

  .center-orb {
    width: 200px;
    height: 200px;
  }

  .hero-title {
    font-size: 2.5rem;
  }

  .hero-buttons {
    flex-direction: column;
    align-items: stretch;
  }

  .about-content {
    grid-template-columns: 1fr;
    padding: 0 1rem;
  }

  .about-layout-grid {
    grid-template-columns: 1fr;
  }

  .contact-content {
    grid-template-columns: 1fr;
    padding: 0 1rem;
  }

  .projects-container {
    grid-template-columns: 1fr;
    padding: 0 1rem;
  }

  .project-image {
    height: 200px;
  }

  .nav-container {
    padding: 0 1rem;
  }
}

@media (max-width: 768px) {
  .nav-links {
    display: none;
  }

  .nav-toggle {
    display: block;
  }

  .nav-menu.active {
    display: flex;
  }

  .hero {
    padding: 0 0.5rem;
  }

  .hero-title {
    font-size: 1.8rem;
  }

  .hero-subtitle {
    font-size: 0.9rem;
  }

  .hero-visual {
    height: 250px;
  }

  .center-orb {
    width: 180px;
    height: 180px;
  }

  .about {
    padding: 3rem 0.5rem;
  }

  .section-title {
    font-size: 1.6rem;
  }

  .stats-grid {
    grid-template-columns: 1fr;
  }

  .skills-grid {
    grid-template-columns: 1fr;
  }

  .projects {
    padding: 3rem 0.5rem;
  }

  .project-image {
    height: 180px;
  }

  .contact {
    padding: 3rem 0.5rem;
  }

  .form-input,
  .form-textarea {
    padding: 0.7rem;
    font-size: 0.85rem;
  }

  .submit-button {
    padding: 0.7rem;
    font-size: 0.85rem;
  }
}

@media (max-width: 480px) {
  .nav-menu .nav-link {
    font-size: 1.2rem;
    padding: 0.8rem;
  }

  .hero-title {
    font-size: 1.6rem;
  }

  .hero-subtitle {
    font-size: 0.85rem;
  }

  .primary-button,
  .secondary-button {
    padding: 0.5rem 1rem;
    font-size: 0.8rem;
  }

  .hero-visual {
    height: 200px;
  }

  .center-orb {
    width: 150px;
    height: 150px;
  }

  .project-image {
    height: 150px;
  }

  .project-title {
    font-size: 1rem;
  }

  .project-description {
    font-size: 0.8rem;
  }

  .section-title {
    font-size: 1.4rem;
  }

  .about-card-title,
  .contact-card-title {
    font-size: 1.1rem;
  }

  .about-paragraph,
  .contact-description {
    font-size: 0.8rem;
  }

  .social-link {
    padding: 0.5rem 0.8rem;
    font-size: 0.75rem;
  }
}

/* Mobile-Specific Overrides */
@media (max-width: 767px) {
  .hero-content {
    grid-template-columns: 1fr !important;
    padding: 0 1rem !important;
    gap: 2rem !important;
  }

  .hero-visual {
    height: 300px !important;
    order: -1;
  }

  .center-orb {
    width: 200px !important;
    height: 200px !important;
  }

  .hero-title {
    font-size: 2.2rem !important;
    line-height: 1.2 !important;
  }

  .hero-subtitle {
    font-size: 1rem !important;
  }

  .section-title {
    font-size: 1.8rem !important;
  }

  .hero-buttons {
    flex-direction: column;
    gap: 1rem;
  }

  .primary-button,
  .secondary-button {
    width: 100%;
    text-align: center;
    justify-content: center;
  }

  .nav-links {
    gap: 0.5rem !important;
  }

  .nav-link {
    padding: 0.5rem 0.8rem !important;
    font-size: 0.85rem !important;
  }

  .about-content {
    grid-template-columns: 1fr !important;
    padding: 0 1rem !important;
  }

  .stats-grid {
    grid-template-columns: 1fr 1fr !important;
  }

  .skills-grid {
    grid-template-columns: 1fr !important;
  }

  .projects-container {
    grid-template-columns: 1fr !important;
    padding: 0 1rem !important;
  }

  .project-image {
    height: 200px !important;
  }

  .contact-content {
    grid-template-columns: 1fr !important;
    padding: 0 1rem !important;
  }

  .contact-stats {
    flex-direction: column;
  }

  .form-input,
  .form-textarea {
    width: 100% !important;
    padding: 0.8rem !important;
  }

  .mobile-hidden {
    display: none !important;
  }

  .mobile-full-width {
    width: 100% !important;
  }
}

@media (max-width: 375px) {
  .hero-title {
    font-size: 1.8rem !important;
  }

  .hero-subtitle {
    font-size: 0.9rem !important;
  }

  .role-tag {
    font-size: 0.8rem !important;
    padding: 0.3rem 0.6rem !important;
  }

  .nav-container {
    padding: 0 0.5rem !important;
  }

  .nav-links {
    gap: 0.3rem !important;
  }
}

/* Accessibility */
@media (prefers-reduced-motion: reduce) {
  * {
    animation: none !important;
    transition: none !important;
  }

  .scroll-behavior {
    scroll-behavior: auto;
  }
}

/* High contrast mode */
@media (prefers-contrast: high) {
  :root {
    --card-border: rgba(255, 255, 255, 0.3);
    --text-secondary: rgba(255, 255, 255, 0.9);
    --text-tertiary: rgba(255, 255, 255, 0.8);
  }

  [data-theme="light"] {
    --card-border: rgba(0, 0, 0, 0.3);
    --text-secondary: rgba(0, 0, 0, 0.9);
    --text-tertiary: rgba(0, 0, 0, 0.8);
  }
}

/* Print styles */
@media print {

  .nav,
  .loader,
  .cursor-trail,
  .particles-container,
  .connected-popup {
    display: none;
  }

  body {
    background: white;
    color: black;
  }

  .container {
    background: white;
  }
}